* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.container {
    margin: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: whitesmoke;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@media (min-width: 320px) and (max-width: 560px) {
    .container {
        width: 100%;
    }
}

@media (min-width:560px) {
    .container {
        width: 560px;
    }
}